@import '../../../base';

.speech-scroll-2 {
  .list-popup-container {
    padding-top: 40px;
    transform: translateX(20px);
    position: fixed;
    z-index: 999;
    transition: min-height 0.3s ease;
    transition: opacity 0.3s ease;
    opacity: 1;
  
    .list-popup-background {
      .arrow {
        position: absolute;
        filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
        content: url(../../../assets/images/forms/triangle.svg);
        right: calc(50% - 10px);
        margin-top: -9px;
        z-index: 30;
  
      }
  
      position: fixed;
      border-radius: 5px;
      background-color: #ffffff;
      box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.3);
    }
  
    .list-popup {
      padding: 12px 20px;
  
      button {
        display: flex;
        align-items: center;
        width: 100%;
        transition: background-color 0.3s ease;
        &:hover,
        &:active {
          text-decoration: none !important;
        }
        
        &:hover {
          background-color: $color-form;
          border-radius: 5px;
        }
  
        .insurer-container {
          margin: 16px 30px;
          padding: 0;
        }
  
        p {
          font-size: $font-size-small;
          font-weight: $font-weight-semibold;
          color: $color-body;
          margin: 0;
        }
  
        &:active {
          background-color: $color-caption;
        }
      }
    }
  }
  
  .list-popup-container-closed {
    min-height: 0;
    opacity: 0;
    pointer-events: none;
  }
  
  .speech-scroll-clickable {
    &:hover {
      cursor: pointer;
    }
  }
}
