@import '../../base';
//navigation wrapper
.navigation {
  z-index: 1030;
  margin: 0px; 
  padding: 0px;
  height: 100%;
  width : 100%;
  font-weight: $font-weight-normal;

  .category-sub {
    height: 100px !important;
    align-items: flex-start;
  }

  .category-main {
    z-index: 1028 !important;
    height: 84px !important;
    background-color: transparent;
    box-shadow: none;
    align-items: center;

    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }

  .category-opaque {
    z-index: 1030 !important;
    background-color: white;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.05);
  }

  .phone-button-container {
    margin: auto 10px auto 0;
    height: 36px;
    border: 1px solid $color-body;
    color: $color-body !important;
    padding: 9px 15px;

    img {
      filter: $color-filter-body;
    }
  }

  .business-button {
    margin-left: 10px;
    button {
      height: 32px;
      transform: translateY(5px);
      p {
        transform: translateY(-6px);
      }
    }
  }

  .business-toggle {
    margin-right: 10px;
    button {
      height: 32px;
      transform: translateY(12px);
      p {
        transform: translateY(-16px);
        height: 32px;
        font-weight: $font-weight-semibold;
        font-size: $font-size-small;
      }
    }
  }

  .account-speech-scroll {
    position: absolute;
    .list-popup-container {
      padding-top: 60px !important;
    }
  }
  .speech-scroll {
    position: absolute;
    .list-popup-container {
      // padding-top: 60px !important;
  
      .list-popup-background {
  
        .arrow {
          position: absolute;
          filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
          content: url(../../assets/images/forms/triangle.svg);
          right: calc(50% - 10px);
          margin-top: -9px;
          width:20px;
          height: 10px;
          z-index: 30;
  
          &:before {
            position: absolute;
            filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
            content: '';
            background-image: url(../../assets/images/forms/triangle.svg);
            right: calc(50% - 10px);
            margin-top: 0px !important;
            width:20px;
            height: 10px;
            z-index: 30;
          }
        }
        position: fixed;
        transform: translateX(20px);
        width: 262px;
        height: 273px;
        border-radius: 5px;
        background-color: #ffffff;
        box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.3);
      }
    }

    .nav-drop-button {
      cursor: pointer;
      height: 62px;
      .nav-font {
        width: auto;
        height: auto;
      }
    }
  }


  .brand-container {
    display: flex;
  }
  .container-fluid {
    padding: 0;

    @media screen and(min-width: 820px){
      padding: 0 15px;
    }
    @media screen and(min-width: 1100px){
      padding: 0 50px;
    }
    @media screen and(min-width: 1200px){
      padding: 0 100px;
    }
  }

  ul{
    list-style: none;
  }
  button, a {
    &:focus {
      outline: none;
    }
  }
  @keyframes nav-animation {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
  // change colors for sign in sign up buttons
  .green-font-col{
    animation: nav-animation  0.2s ease-in-out 0s 1 forwards !important;
    button{
      border: solid 1px #354f53 !important;
      p{
        color: #354f53 !important;
      }
    }
  }
  .white-font-col{
    animation: nav-animation  0.2s ease-in-out 0s 1 forwards !important;
    button{
      border: solid 1px #ffffff !important;
      p{
        color: #ffffff !important;
      }
    }
   
  }
  // indication if page link is navigation link
  .page-indication {
    
    .nav-link {
      font-weight: $font-weight-heavy !important;
      color: $color-title;


      &:hover {
        color: $color-hover;
      }
    }

    .page-indicator{
      margin: -10% auto 0 auto;
      width: 15px;
      height: 4px;
      border-radius: 4px;
      background-color: $color-title;
    }
  }
  
  .insurance-nav {

    .arrow {
      position: absolute;
      filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
      content: url(../../assets/images/forms/triangle.svg);
      right: calc(50% - 10px);
      margin-top: -9px;
      width:20px;
      height: 10px;
      z-index: 30;
      
      &:before {
        position: absolute;
        filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
        content: '';
        background-image: url(../../assets/images/forms/triangle.svg);
        right: calc(50% - 10px);
        margin-top: 0px !important;
        width:20px;
        height: 10px;
        z-index: 30;
      }
    }
  }

  .more-nav {

    .arrow {
      position: absolute;
      filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
      content: url(../../assets/images/forms/triangle.svg);
      right: calc(50% - 10px);
      margin-top: -9px;
      width:20px;
      height: 10px;
      z-index: 30;
      
      &:before {
        position: absolute;
        filter: brightness(0) invert(1) drop-shadow(0px -5px 10px rgba(0, 0, 0, 0.1));
        content: '';
        background-image: url(../../assets/images/forms/triangle.svg);
        right: calc(50% - 10px);
        margin-top: 0px !important;
        width:20px;
        height: 10px;
        z-index: 30;
      }
    }
  }
  .user-nav {
    .dropdown-item {
       &:active {
         background-color: #F2FAF9;
       }
    }
  }
  .nav-drop-button {
    cursor: pointer;
    padding: 0 20px;

    .nav-font {
      width: auto;
      height: auto;
    }

  }

  .navbar{
    height:60px;
    @media screen and (min-width: 820px) {
      height:84px;
    }
  }
  .nav-white-bg{
    background-color: rgba(255,255,255, 0);
    // box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.05);
    transition: background-color 0.3s ease;
  }
  .nav-white-bg-opaque {
    background-color: rgba(255,255,255, 0.9);
    transition: background-color 0.3s ease;
  }
  .nav-no-bg{
    background-color: rgba(255,255,255,0);
    // box-shadow: none;
  }

  .nav-main-links{
    font-size: $font-size-smallest;
    font-weight: 600;
    line-height: 3.93;
    letter-spacing: -0.1px;
    text-align: center;
    .green-font-col{
      color: $color-title;

      &:hover {
        color: $color-hover;
      }
    }

    .category-style {
      .button-small-outline {
        border: 1px solid $color-input-form-landing-desc2 !important;
        margin-right: 10px !important;

        &:hover {
          border: 1px solid $color-hover !important;
        }

        p {
          margin-top: -6px !important;
          color: $color-input-form-landing-desc2;
          font-size: $font-size-tiny;
          transform: translateY(-2px);
        }
        
      }
      .button-medium-flat-light-green {
        height: 36px;

        p {
          margin-top: -3px;
          font-size: $font-size-tiny;
          transform: translateY(-2px);
        }
      }
    }
    

    .green-hover-col {
      color: $color-hover !important;
    }
    .white-font-col{
      color: #ffffff !important;
    }
    // for checkout- details page no
    .display-none{
      display: none;
    }
    .nav-link {
      padding: 0;
    }
    .nav-ul{
      padding-left: 0;
      width: 110px;

      .nav-item{
        margin:0 !important;
        
        .arrow{
          transform-origin: 60%;
          &:before{
            filter: brightness(0) saturate(100%);
            -webkit-filter: brightness(0) saturate(100%);
          }
          filter: invert(28%) sepia(26%) saturate(476%) hue-rotate(140deg) brightness(93%) contrast(92%);
          -webkit-filter:  invert(28%) sepia(26%) saturate(476%) hue-rotate(140deg) brightness(93%) contrast(92%);
          padding-left: 5px;
          transition: 0.2s ease-in-out;
        }
        .turn{
          transform-origin: 60%;
          filter:  invert(28%) sepia(26%) saturate(476%) hue-rotate(140deg) brightness(93%) contrast(92%);
          -webkit-filter:  invert(28%) sepia(26%) saturate(476%) hue-rotate(140deg) brightness(93%) contrast(92%);
          padding-left: 5px;
          -ms-transform: rotate(180deg) !important;
          -webkit-transform: rotate(180deg) !important;
          transform: rotate(180deg) !important;
          transition: 0.2s ease-in;
        }
      }
    }
    .user-ul {
      width:130px;

      @media screen and(min-width: 1120px) {
        width:180px;
        margin-left: 10px;
      }

      .dropdown {
        height: 100%;
      }

      .profile-pic {
        width: 50px;
        float: left;

        img {
          width: 40px;
          border-radius: 50%;
          margin-left: -20px;
          object-fit: cover;
          border: 1px solid $color-title;
        }
      }

      .user-name {
        float: left;
        margin: auto;
        p {
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          max-width: 90px;

          @media screen and(min-width: 1200px) {
            max-width: 140px;
          }
        }
      }

      .profile-img-container {  
        .arrow {
          transition: 0.2s ease-in;
        }
  
        .turn {
          -ms-transform: rotate(180deg) !important;
          -webkit-transform: rotate(180deg) !important;
          transform: rotate(180deg) !important;
          transition: 0.2s ease-in
        }
      }

    }
  }
  .nav-font {
    width: 46px;
    height: 22px;
    font-size: $font-size-small;
    text-decoration: none;
    color: $color-title;
  }
  .blog-a {
    width: 50px !important;

    @media screen and(min-width: 1000px){
      width: 80px !important;
    }
  }
  .dropdown-menu {
    border-radius: 5px !important;
    border: none;
    background-color: #ffffff !important;
    box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.3) !important;

    &:after,
    &:before {
      content: "";
      position: absolute;
      top: -27px;
      left: 20px;
      width: 0;
      height: 0; 
      border-left: 20px solid transparent;
      border-right: 20px solid transparent;
      border-bottom: 20px solid #ffffff;
      border-top: 10px solid transparent;
    }

    .dropdown-item {
      cursor: pointer;

      &:hover,
      &:active,
      &:after {
        background-color: #B2F2E6;
      }
      .nav-font {
        color: $color-body;
      }
    }
    .more {
      border-radius: 5px !important;
      background-color: $color-form;
      margin-bottom: -10px;
    }
  }
  .nav-icon {
    width: 25px;
    margin-right: 20px;
  }
  .button-small-outline {
    width: 97px;
    height: 36px;
    border: solid 1px $color-title !important;
    background-color: rgba(255,255,255,0);
    margin-right: 25px;
    margin: auto !important;
    p {
      color:  $color-title !important;
      margin-top: -10px !important;
    }
    &:hover {
      border: solid 1px $color-hover !important;
      p{
        color: $color-hover !important;
      }
    }
  }
  .button-small-flat-dark {
    width: 97px;
    height: 36px;
    margin: auto 0px auto 10px !important;

    p {
      margin-top: -10px !important;
    }
  }
  // mobile navigation css
  .mobile-nav {
    width: 100vw;
    height: 60px;
    padding: 0 20px;
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 1032;
    
    // Mobile view
    .navbar-toggler{
      @media screen and (max-width: 820px) {
        display: block !important;
      }
    }

    .nav-mobile-opened {

      .rodal-dialog {
        width: 100% !important;
        height: 100% !important;
        background-image: linear-gradient(147deg, #00d7af, #46737a);
        opacity: 0.9 !important;
        overflow: auto !important;
    
        .PP-logo {
          width: 100px;
          color: #ffffff;
          font-family: 'Lato';
          font-size: $font-size-big;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          margin-bottom: 35px;
          padding: 0 15px;
        }
    
        background-color: rgba(255,255,255,0);
        border: none;
        .rodal-close {
          margin-top: 10px;
          margin-right: 5px;
          background-image: url(../../assets/images/common/CloseIcon.svg);
          background-repeat: no-repeat;
          filter: brightness(0) invert(1);
          
          &:hover {
            filter: $color-filter-hover;
          }
        }
        .rodal-close:before,
        .rodal-close:after, {
          background: none;
        }
        .profile-pic {
          width: 50px;
          float: left;
    
          img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
            border: 1px solid #fff;
          }
        }
    
        .user-name {
          margin-left: 20px;
          p {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            max-width: 100px;
            @media screen and(min-width: 360px) {
              max-width: 250px;
            }
            @media screen and(min-width: 768px) {
              max-width: 500px;
            }
          }
        }
    
        .button-container {
          display: flex;
          padding: 0 20px;

          .button-small-outline {
            cursor: pointer;
            width: 140px;
            height: 60px;
            border-radius: 36px;
            border: solid 1px #ffffff !important;
            background-color: rgba(255,255,255,0);
            margin: 0 !important;
            
            p {
              color: #ffffff !important;
              font-weight:$font-weight-semibold;
              font-size:$font-size-small;
              margin: 0 !important;
            }
          }
          .button-small-flat {
            cursor: pointer;
            background-image: linear-gradient(to bottom, #ffffff, #ffffff) !important;
            width: 140px;
            height: 60px;
            border-radius: 36px;
            p{
              color: #00d7af !important;
              font-weight:$font-weight-semibold;
              font-size:$font-size-small;
              margin: 0 !important;
            }
          }
        }
        .nav-item {
          list-style: none;
          text-align: left;
          padding: 0 15px;

          .dropdown-toggle,
          .nav-link {
            padding-top: 27px;
            padding-left: 0;
            display: flex;
            align-items: center;
            
            .arrow-container {
              display: flex;
              align-items: center;
              img {
                position: absolute;
                right: 15px;
              }
            }
          }
          .sub-nav-menu{
            background-color: rgba(255, 255, 255, 0) !important;
            border: none;
            padding: 0;
            margin: 0 auto;
            .nav-link{
              padding: 10px;

              button {
                padding: 0 0 0 57px;
                &:active,
                &:focus {
                  background-color: rgba(255, 255, 255, 0) !important;
                }
              }
            }
            .dropdown-item{
              border-radius: 8px;
              display: flex;
              min-height: 50px;
              .nav-font{
                color:#ffffff;
                font-size: $font-size-small;
                display: flex;
    
                .nav-icon{
                  color: #ffffff !important;
                  min-width: 30px;
                  min-height: 30px;
                  margin-top: -5px;
                  margin-right: 30px !important;
                }
              }
              &:hover{
                border: none;
                transition: 0.2s ease-in-out;
                background-color: rgba(255, 255, 255,0.1) !important;
              }
              &:active{
                border: none;
                transition: 0.2s ease-in-out;
                background-color: rgba(255, 255, 255,1) !important;
              }
              img{
                margin-right: 15% !important;
              }
            }
          }
          .nav-link{
            color: #ffffff !important;
            font-size: $font-size-big;
            font-weight: 650;
            
            .arrow{
              transition: 0.2s ease-in-out;
            }
            .turn{
              color:$color-filter-title;
              transform: rotate(180deg) !important;
              transition: 0.2s ease-in;
            }
          }
        }  
      }
    }
  }
}
