@import '../../../base';

.go-to-app-on-mobile {
  padding-top: 60px;

  > .container {
    background-color: $color-hover-alt;
    padding: 20px 18px 15px 18px;
    max-width: unset;

    @media screen and (max-width: 375px) {
      padding: 22px 14px 15px 14px;
    }
  }

  .btn-redirect {
    min-width: 90px;
    min-height: unset !important;
    width: auto !important;
    height: auto !important;
    padding: 8px 12px;

    &:hover, &:focus {
      outline: none;
    }

    @media screen and (max-width: 375px) {
      min-width: unset !important;
      padding: 7px 12px;
    }

    > p {
      white-space: nowrap;
    }

    .text {
      @media screen and (max-width: 375px) {
        display: none;
      }
    }

    .icon {
      font-size: 1.2rem;

      @media screen and (min-width: 376px) {
        display: none;
      }
    }
  }

  [class^="app"] {
    margin-bottom: 0;
    color: #fff;
  }

  .app {
    &-icon {
      padding: 10px;
      background-color: #fff;
      border-radius: 12px;
    }

    &-name {
      font-weight: bold;
      font-size: 1.2rem;
    }

    &-desc {
      font-size: 1rem;

      @media screen and (max-width: 375px) {
        font-size: 0.875rem;
      }
    }
  }

  .close {
    position: absolute;
    text-shadow: none;
    font-weight: normal;
    font-size: 1rem;
    color: #fff;
    background-color: $color-body;
    padding: 3px 5px;
    border-bottom-right-radius: 12px;
  }
}